body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  overflow: hidden;
  flex-direction: column;
}

/* #####################
    CSS for nav Bar
   #################### */

.navbar {
  overflow: hidden;
  padding-top: 50px;
  padding-left: 50px;
  background: #3f51b5;
  font-family: Arial, Helvetica, sans-serif;
  border-bottom: 1px solid #d1e8fa;
}

.navbar a {
  float: left;
  font-size: 1rem;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.dropdown {
  float: left;
  overflow: hidden;
}

.dropdown .dropbtn {
  font-size: 1rem;  
  border: none;
  outline: none;
  color: white;
  padding: 14px 16px;
  background-color: inherit;
  font: inherit;
  margin: 0;
}

.navbar a:hover, .dropdown:hover .dropbtn {
  cursor: pointer;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: white;
  width: 90%;
  left: 5%;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.button {
  background-color: #3f51b5;
  border: none;
  color: white;
  padding: 10px 24px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 15px;
  cursor: pointer;
}

.detailsButton {
  border: 1px solid #3f51b5;
  color: #000;
  padding: 10px 24px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 15px;
  cursor: pointer;
}

.w50 {
  width: 50%;
}

.mt10 {
  margin-top: 10px
}

/* Create three equal columns that floats next to each other */
.column {
  float: left;
  width: 15%;
  padding: 10px;
  height: 250px;
}

.column a {
  float: none;
  color: black;
  padding: 8px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.odd_column {
  background: #f9f9f9;
}

.sub-menu {
 font-size: 1rem;
 font-weight: 700;
 text-transform: capitalize;
}
/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive layout - makes the three columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
    height: auto;
  }

  .navbar {
    padding-left: 20px;
  }

  .navbar a, .dropdown .dropbtn {
    font-size: 0.5rem;
    padding: 14px 5px;
  }
}

.productionTitle {
  position: relative;
  display: flex;
  font-family: "Archivo Narrow", sans-serif;
  font-weight: 700;
  color: #1d1d1d;
  line-height: 100px;
  text-transform: uppercase;
  padding-left: 10px;
}

.productionTitle:before {
  content: attr(data-mask);
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%);
  left: -4px;
  z-index: -1;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 100px;
  line-height: 1em;
  color: #f4f4f4;
  letter-spacing: .08em;
  padding-left: 10px;
}

#productCompareBtn {
  position: fixed;
  bottom: 25px;
  right: 25px;
  padding: 8px;
}

#productCompareBtn:hover {
  border-radius: 2px;
  background-color: #fff;
  box-shadow: 0 1px 2px 0 rgba(0,0,0,.2);
  border: 1px solid #f0f0f0;
  transform: scale(1);
}
#productCompareBtn:hover #productDetailsDiv {
  display: block;
}
#productCompareBtn:hover .productBtn {
  float: right;
}

#productDetailsDiv {
  display: none;
}




